/* 기본 스타일 */
.movie-row {
    margin: 20px 0;
}

.movie-row h2 {
    margin-left: 20px;
    color: #333333;
    font-size: 1.5em;
}

.scroll-container-wrapper {
    display: flex;
    align-items: center;
    position: relative;
}

.scroll-container {
    display: flex;
    overflow-x: auto;
    padding-left: 20px;
    gap: 10px;
    scroll-snap-type: x mandatory;
    -ms-overflow-style: none; /* IE 및 Edge에서 스크롤 바 숨기기 */
    scrollbar-width: none; /* Firefox에서 스크롤 바 숨기기 */
}

.scroll-container::-webkit-scrollbar {
    display: none; /* Chrome, Safari에서 스크롤 바 숨기기 */
}

.movie-card-wrapper {
    width: 14%;
    scroll-snap-align: start;
    flex: 0 0 auto;
}

.arrow-button {
    border: none;
    color: #333333;
    font-size: 1.5em;
    cursor: pointer;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin: 0 10px;
}

.arrow-button:hover {
    background-color: rgba(0, 0, 0, 0.8);
}

/* 반응형 스타일 */
@media (max-width: 1024px) {
    .movie-card-wrapper {
        width: 20%;
    }
}

@media (max-width: 768px) {
    .movie-card-wrapper {
        width: 25%;
    }
}

@media (max-width: 600px) {
    .movie-card-wrapper {
        width: 33.33%;
    }
}

@media (max-width: 480px) {
    .movie-card-wrapper {
        width: 50%;
    }
}

@media (max-width: 320px) {
    .movie-card-wrapper {
        width: 100%;
    }
}
